  methods: {
    hideOriginal: function () {
      this.showOrignal = false;
    },
    doParty: function () {
      this.hoera = true;
      this.buttonText = "Meer confetti!!!";
      let e = document.querySelector(".button");
      party.confetti(e, {
        count: party.variation.range(20, 40),
        spread: 100,
      });
